@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.authInput {
    max-width: 340px;
    width: 100%;
    padding: 10px;
    padding-left : 20px;
    padding-right : 20px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 14px;
    color: black;
}
  
.authSubmit {
    max-width: 360px;
    width: 100%;
    padding: 10px;
    text-align: center;
    border-radius: 30px;
    background: #04aaff;
    color: white;
    margin-top: 15px;
    margin-bottom: 10px;
    font-size: 14px;
    cursor: pointer;
}
  
.authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}
  
.authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}
.authContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
}
  
.authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 380px;
}
  
.authBtn {
    cursor: pointer;
    border-radius: 20px;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 180px;
    background: white;
    cursor: pointer;
}
body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.tweetEdit {
    display: flex;
    flex-direction: column;
}

.tweetEdit .formBtn {
    cursor: pointer;
    margin-bottom: 5px;
}

.updateBtn {
  margin-top : 10px;
  background-color: #04aaff;
}
  
.tweet {
    margin-bottom: 20px;
    background-color: #343434;
    width: 100%;
    max-width: 380px;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    display: flex;
    flex-direction: row;
    color: rgba(0, 0, 0, 0.8);
}

.tweet_content {
    display : flex;
    flex-direction : column;
}

.creator_img_frame {
    width : 50px;
    height : 50px;
    border-radius : 50%;
    overflow : hidden;
}

.creator_img {
    width : 100%;    
}
 
.tweet_content {
    padding : 20px;
    padding-top : 5px;
    padding-right : 0;
    padding-bottom : 5px;
}

.tweet h1 {
    color : #F2F2F2;
    font-size : 16px;
    font-weight: 500;
}

.tweet h2 {
    color : #a3a3a3;
    margin-top : 6px;
    font-size : 15px;
    font-weight: 400;
}

.tweet h4 {
    color : #F2F2F2;
    font-size: 14px;
    margin-top : 20px;
    width : 280px;
    line-height : 20px;
}

.tweet_imgFrame {
    margin-top : 30px;
    height : 150px;
    width : 280px;
    border-radius : 10px;
    display: flex;
    justify-content: center;
    overflow : hidden;
    background-color : #282727;
}
  
.tweet_img {
    height : 100%;
}
  
.tweet__actions {
    position: absolute;
    right: 10px;
    top: 10px;
}
  
.tweet__actions span {
    cursor: pointer;
}
  
.tweet__actions span:first-child {
    margin-right: 10px;
}

.tweet__icons {
    margin-top : 30px;
}

.tweet__icons span {
    cursor : pointer;
}

.tweet__icons span:first-child {
    margin-right : 20px;
}
.factoryForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
  
.factoryInput__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}
  
.factoryInput__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 20px;
    font-weight: 300;
    font-size: 14px;
    margin-top : 40px;
}
  
.factoryInput__arrow {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
    margin-top : 40px;
    outline : none;
    border : none;
    cursor : pointer;
}
  
.factoryInput__label {
    color: #04aaff;
    cursor: pointer;
}
  
.factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
}
  
.factoryForm__attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 200px;
    overflow : hidden;
}
  
.factoryForm__attachment img {
    width : 100%;
    height : 100%;
}
  
.factoryForm__clear {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
    margin : 20px;
}
  
.factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
}

.factoryForm__img {
    white-space : pre;
}

.factory__hello {
    font-size : 2rem;
    font-weight: 300;
    padding-left : 10px;
    line-height: 2.4rem;
}

.user__name {
    display : inline-block;
    font-weight: 500;
}

#attach-file {
    opacity : 0;
}
.tweet__list {
    margin-top : 30;
}
.getTweets {
    background-color :  #04aaff;
    margin-top : 30px;
    margin-bottom : 20px;
}
.profileForm {
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    padding-bottom: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}
  
.logOut {
    background-color: tomato;
    cursor: pointer;
    box-sizing: border-box;
}

.updateBtn {
    background-color: #04aaff;
    margin-top : 10;
    box-sizing: border-box;
}

.profile__label {
    color: #04aaff;
    cursor: pointer;
    margin-top : 20px;
}
  
.profile__label span {
    margin-right: 10px;
    font-size: 12px;
}

.profile__photoForm {
    display : flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profile__photo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 140px;
    height : 140px;
    border-radius: 50%;
    overflow : hidden;
}
  
.profile__photo img {
    width : 100%;
}
.navigation__ul {
    display : flex; 
    justify-content : center; 
    margin : 50px;
    margin-bottom : 30px;
}

.home__link {
    margin : 20px;
}

.profile__link {
    margin : 20px;
    margin-top : 5px;
    display : flex;
    flex-direction : column;
    align-items : center;
    font-size : 12px;
}
.router__container {
    max-width : 890; 
    width : 100%; 
    margin : 0 auto; 
    margin-top : 50; 
    display : flex; 
    justify-content : center;
}
* {
  box-sizing: border-box;
  -moz-box-sizing : border-box;
  -webkit-box-sizing: border-box;
}

input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

body {
  background-color: #393E46;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
  overflow-y : scroll;
}

body::-webkit-scrollbar {
  display : none;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.tweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.tweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.tweet-input::-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.tweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 380px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

.formInput {
  width: 100%;
  margin-top : 5px;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid #04aaff;
  text-align: center;
  background-color: transparent;
  max-width: 380px;
  color: white;
  box-sizing: border-box;
}

.formBtn {
  cursor: pointer;
  width: 100%;  
  max-width: 380px;
  padding: 10px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  box-sizing: border-box;
}

.cancelBtn {
  cursor: pointer;
  margin-top : 5px;
  background-color: tomato;
  box-sizing: border-box;
}
