.authContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
}
  
.authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 380px;
}
  
.authBtn {
    cursor: pointer;
    border-radius: 20px;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 180px;
    background: white;
    cursor: pointer;
}